import {Box} from "../container/box";
import {router,IRoute} from "./router";

export enum ERouterViewMode{
	HASHBANG,NORMAL
}

export interface IRouterViewConfig{
	name:string;
	mode?:ERouterViewMode;
}

export class RouterView extends Box{
	private name:string;
	private mode:ERouterViewMode;
	constructor(config:IRouterViewConfig){
		super();
		this.name = config.name;
		this.mode = config.mode || ERouterViewMode.HASHBANG;
		let index = (<IRoute[]>router["routes"])
						.findIndex(route => route.routerView === this.name);
		if(index > -1){
			(<IRoute[]>router["routes"])[index].routerViewInstance = this;
		}
	}
}
